.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f5f5f5;
}

.meeting-container {
  display: flex;
  flex: 1;
  width: 100%;
  background: #fff;
  overflow: hidden;
  position: relative;
}

/* Video Grid */
.video-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 10px;
  padding: 10px;
  background: #333;
}

.video-tile {
  background: #444;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  aspect-ratio: 16 / 9;
}

/* Sidebar */
.sidebar {
  width: 300px;
  background: #f9f9f9;
  border-left: 2px solid #ddd;
  display: flex;
  flex-direction: column;
  animation: slide-in 0.3s ease-out;
}

@keyframes slide-in {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

/* Chat Section */
.chat-section {
  flex: 1;
  padding: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.chat-messages {
  flex: 1;
  background: #eaeaea;
  border-radius: 5px;
  padding: 10px;
  overflow-y: auto;
}

.chat-input {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  width: 100%;
}

/* Controls Section */
.controls {
  display: flex;
  justify-content: center;
  gap: 10px;
  padding: 10px;
  background: #fff;
}

.control-button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.control-button:hover {
  background: #ddd;
}

.end-call {
  background: #ff4d4d;
  color: #fff;
}

.end-call:hover {
  background: #e60000;
}