/* RoomManagement.css */

.room-container {
    display: flex;
    flex-direction: row;
    gap: 20px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .create-room {
    flex: 1;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .room-list {
    flex: 2;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    max-height: 600px;
  }
  
  h2 {
    font-size: 1.5em;
    margin-bottom: 15px;
    color: #333;
  }
  
  form input[type="text"],
  form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1em;
  }
  
  form button {
    padding: 10px 20px;
    font-size: 1em;
    background-color: #4caf50;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  form button:hover {
    background-color: #45a049;
  }
  
  .error {
    color: red;
    margin-bottom: 15px;
  }
  
  .room-item {
    padding: 15px;
    border-bottom: 1px solid #e0e0e0;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .room-item h3 {
    font-size: 1.2em;
    color: #333;
    margin: 0;
  }
  
  .room-item p {
    color: #777;
    font-size: 0.9em;
    margin: 5px 0 0;
  }
  
  .room-item:hover {
    background-color: #f0f0f0;
  }
  